import { DatasetScope } from "@rp/common/api-types"
import { chatDatasets } from "@rp/react/common/chat"
import { Typography } from "@rp/react/web/mui/core"
import { ChatFilterChip } from "./ChatFilterChip"

type Props = {
  scope: DatasetScope
  onRemove: () => void
  onClick: () => void
}
const ScopeChip = ({ scope, onRemove, onClick }: Props) => {
  return (
    <ChatFilterChip label="Sources:" onClose={onRemove} onClick={onClick}>
      <Typography sx={{ whiteSpace: "nowrap" }}>{chatDatasets[scope].label}</Typography>
    </ChatFilterChip>
  )
}
export { ScopeChip }
